/* Very tiny grid system using flex box */
.row {
  display: flex;
  flex-direction: row;
}
.column {
  display: flex;
  flex-direction: column;
}
.center {
  display: flex;
  align-items: center;
  justify-content: center;
}
.center-x {
  display: flex;
  justify-content: center;
}
.center-y {
  display: flex;
  align-items: center;
}
.flex {
  flex: 1;
}
.hidden {
  display: none;
}
.relative {
  position: relative;
}
.absolute {
  position: absolute;
}
.full-width {
  width: 100%;
}
.full-height {
  height: 100%;
}
.full-size {
  left: 0;
  right: 0;
  bottom: 0;
  top: 0;
}
.right {
  display: flex;
  justify-content: flex-end;
}
.left {
  display: flex;
  justify-content: flex-start;
}
input[type=text],
input[type=password],
input[type=email],
textarea {
  border: 1px solid #ccc;
  padding: 5px;
  color: #333;
  margin: 0;
  outline: 0 none;
  border-radius: 3px;
  box-shadow: inset -1px 1px 1px rgba(0, 0, 0, 0.1);
}
input[type=text]:focus,
input[type=password]:focus,
input[type=email]:focus,
textarea:focus {
  border-color: rgba(52, 152, 219, 0.75);
}
input[type=text]:disabled,
input[type=password]:disabled,
input[type=email]:disabled,
textarea:disabled {
  background: rgba(0, 0, 0, 0.025);
}
textarea {
  width: 100%;
  min-height: 100px;
  resize: vertical;
}
.form-input {
  position: relative;
}
.form-input input {
  width: 100%;
  padding-right: 25px;
}
.form-input.is--ok {
  border-color: #2ecc71 !important;
}
.form-input.is--error {
  border-color: #e74c3c !important;
}
.form-input div.validation-icon {
  position: absolute;
  top: 6px;
  right: 9px;
}
.form-input div.validation-icon svg.ok {
  color: #2ecc71;
}
.form-input div.validation-icon svg.fail {
  color: #e74c3c;
}
.form-input div.validation-message {
  display: flex;
  flex-direction: row;
  justify-content: flex-end;
  margin: 5px 2px 0 0;
  position: relative;
}
.form-input div.validation-message:before {
  position: absolute;
  display: block;
  content: '';
  top: -4px;
  right: 10px;
  width: 0;
  height: 0;
  -webkit-transform: rotate(360deg);
  border-style: solid;
  border-width: 0 4px 4px 4px;
  border-color: transparent transparent rgba(0, 0, 0, 0.7) transparent;
}
.form-input div.validation-message span {
  background: rgba(0, 0, 0, 0.7);
  border-radius: 3px;
  padding: 3px 6px;
  display: inline-block;
  font-size: 12px;
  color: #fff;
}
button {
  color: #fff;
  background: #e74c3c;
  padding: 5px 15px;
  outline: 0 none;
  border: 0 none;
  border-radius: 3px;
  box-shadow: inset 0 1px rgba(255, 255, 255, 0.2), inset 0 -1px rgba(0, 0, 0, 0.3);
  transition: all 0.085s;
  position: relative;
}
button:hover {
  box-shadow: inset 0 1px rgba(255, 255, 255, 0.2), inset 0 -1px rgba(0, 0, 0, 0.3), 0 0 7px rgba(52, 152, 219, 0.5);
}
button:active {
  background: #333;
  cursor: pointer;
}
button[disabled] {
  box-shadow: none !important;
  background: #ccc;
  cursor: not-allowed;
}
button.has--icon {
  flex-direction: row;
  display: flex;
  align-items: center;
}
button.has--icon svg {
  margin: 0 10px 0 0;
}
button svg {
  z-index: 100;
}
button span.text {
  z-index: 100;
  display: block;
  position: relative;
}
button span.progress {
  z-index: 50;
  position: absolute;
  height: 100%;
  top: 0;
  left: 0;
  border-radius: 3px;
  background: rgba(0, 0, 0, 0.15);
  display: block;
}
.buttons {
  margin: 10px 0 0 0;
}
.fieldset-container {
  display: flex;
  flex-direction: row;
}
.fieldset-container fieldset {
  flex: 1;
  margin: 5px 10px;
}
fieldset {
  border: 1px solid #ccc;
  position: relative;
  border-radius: 3px;
  margin: 0 0 10px 0;
  padding: 5px 5px 5px 10px;
  background: #fff;
  box-shadow: 0 1px 0 rgba(0, 0, 0, 0.1);
}
fieldset:last-child {
  margin: 0;
}
fieldset legend {
  color: #333;
  border: 1px solid #ccc;
  padding: 3px 7px;
  border-radius: 3px;
  background: #fff;
}
fieldset .form-field {
  display: flex;
  flex-direction: row;
  margin: 0 0 5px 0;
  position: relative;
}
fieldset .form-field.form-field-path input[type=file] {
  display: none;
}
fieldset .form-field.form-field-path button {
  margin-left: 5px;
}
fieldset .form-field.form-field-path .error-container {
  right: 54px;
}
fieldset .form-field.form-field-path.has-info .error-container {
  right: 84px;
}
fieldset .form-field:last-child {
  margin: 0;
}
fieldset .form-field label {
  width: 100px;
  font-size: 14px;
  line-height: 30px;
}
fieldset .form-field label.w-100 {
  width: 100px;
}
fieldset .form-field label.w-150 {
  width: 150px;
}
fieldset .form-field label.w-200 {
  width: 200px;
}
fieldset .form-field label.w-250 {
  width: 250px;
}
fieldset .form-field input {
  flex: 1;
}
fieldset .form-field input.error:not([disabled]) {
  border-color: #e74c3c;
}
fieldset .form-field .error-container {
  display: flex;
  align-items: center;
  justify-content: center;
  position: absolute;
  top: 8px;
  right: 5px;
  width: 16px;
  height: 16px;
  border-radius: 10px;
}
fieldset .form-field .error-container i {
  color: #e74c3c;
}
fieldset .form-field.has-info .error-container {
  right: 35px;
}
.file-input button {
  margin: 0 0 0 5px;
}
.file-input input[type=file] {
  display: none;
}
.table {
  flex: 1;
  overflow-y: auto;
  margin: 10px 0;
  border: 1px solid #ccc;
  border-radius: 3px;
  background: #fff;
  box-shadow: 0 1px 0 rgba(0, 0, 0, 0.1);
}
table {
  border-spacing: 0;
  border-collapse: collapse;
  border: 0;
  margin: 0;
  padding: 0;
  width: 100%;
  font-size: 14px;
}
table thead tr th {
  border-bottom: 1px solid #ccc;
  padding: 5px 10px;
  text-align: left;
}
table tbody tr td {
  padding: 3px 10px;
}
.checkbox {
  flex-direction: row;
  display: flex;
  align-items: center;
}
.checkbox label {
  flex: 1;
  margin: 0 0 0 5px;
}
.checkbox .container {
  cursor: pointer;
}
.checkbox .container.active svg {
  color: #27ae60;
}
.checkbox .container svg {
  font-size: 18px;
}
.info {
  display: flex;
  align-items: center;
  height: 30px;
  width: 30px;
  padding: 0 5px;
  position: relative;
}
.info .info-button {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 20px;
  height: 20px;
  background: #2980b9;
  color: #fff;
  border-radius: 100%;
  cursor: pointer;
}
.info .info-button:hover + .info-text {
  display: block;
}
.info .info-text {
  position: absolute;
  top: 50%;
  right: 50%;
  background: rgba(0, 0, 0, 0.75);
  color: #fff;
  padding: 5px 10px;
  border-radius: 3px;
  width: 250px;
  font-size: 12px;
  display: none;
  z-index: 5;
}
.info .info-text:hover {
  display: block;
}
.modal-container {
  display: flex;
  align-items: center;
  justify-content: center;
  position: fixed;
  z-index: 9998;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  transition: opacity 0.3s ease;
}
.modal {
  background-color: #fff;
  border-radius: 2px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.33);
  transition: all 0.3s ease;
  font-family: Helvetica, Arial, sans-serif;
}
.modal .body {
  padding: 15px;
}
/*
 * The following styles are auto-applied to elements with
 * transition="modal" when their visibility is toggled
 * by Vue.js.
 *
 * You can easily play with the modal transition by editing
 * these styles.
 */
.modal-enter {
  opacity: 0;
}
.modal-leave-active {
  opacity: 0;
}
.modal-enter .modal,
.modal-leave-active .modal {
  -webkit-transform: scale(1.1);
  transform: scale(1.1);
}
.progress-bar-container {
  width: 100%;
  height: 20px;
  background: #ccc;
  border-radius: 3px;
  position: relative;
}
.progress-bar-container .progress-bar {
  height: 20px;
  background: #3498db;
  border-radius: 3px;
  transition: width 0.125s;
}
.progress-bar-container .progress-bar-text {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  text-align: center;
  font-size: 12px;
  line-height: 20px;
}
.select {
  position: relative;
  z-index: 150;
}
.select.focus div.selected-value {
  border-color: rgba(52, 152, 219, 0.75);
  border-radius: 3px 3px 0 0;
  border-bottom-color: #ccc;
}
.select.focus div.dropdown {
  display: block;
  border-color: rgba(52, 152, 219, 0.75);
}
.select div.selected-value {
  border: 1px solid #ccc;
  padding: 5px;
  color: #333;
  margin: 0;
  outline: 0 none;
  border-radius: 3px;
  box-shadow: inset -1px 1px 1px rgba(0, 0, 0, 0.1);
}
.select div.selected-value span.nothing-selected {
  color: #999;
}
.select div.selected-value div.arrow {
  margin: 0 3px 0 0;
}
.select div.dropdown {
  position: absolute;
  top: 100%;
  left: 0;
  right: 0;
  background: #fff;
  border: 1px solid #ccc;
  border-top: none;
  border-radius: 0 0 3px 3px;
  padding: 5px;
  display: none;
  z-index: 10;
}
.select div.dropdown svg.is--creating {
  position: absolute;
  top: 13px;
  right: 13px;
}
.select div.dropdown ul.values {
  list-style: none;
  margin: 5px 0 0 0;
  padding: 0;
  max-height: 150px;
  overflow-y: auto;
  position: relative;
}
.select div.dropdown ul.values li {
  padding: 5px;
}
.select div.dropdown ul.values li.disabled {
  opacity: 0.5;
  cursor: not-allowed;
}
.select div.dropdown ul.values li.disabled:hover {
  background: none;
  cursor: not-allowed;
}
.select div.dropdown ul.values li.active {
  background: rgba(0, 0, 0, 0.1);
  font-weight: bold;
}
.select div.dropdown ul.values li:hover {
  background: rgba(0, 0, 0, 0.1);
  cursor: pointer;
}
.select div.dropdown span.nothing-found {
  padding: 5px 0;
  font-size: 12px;
}
.select div.dropdown span.nothing-found span.filter-term {
  font-weight: bold;
  font-size: 12px;
  white-space: pre;
}
.select div.dropdown span.nothing-found span.create-item {
  font-size: 12px;
  color: #aaa;
}
.tab-menu > ul {
  list-style: none;
  margin: 0;
  padding: 0;
  display: flex;
  flex-direction: row;
  border-top: 1px solid #ccc;
  border-bottom: 1px solid #ccc;
}
.tab-menu > ul li {
  padding: 5px 15px;
  font-size: 16px;
  cursor: pointer;
  display: flex;
  flex-direction: row;
  align-items: center;
}
.tab-menu > ul li.active {
  background: rgba(0, 0, 0, 0.1);
}
.tab-menu > ul li span {
  font-size: 14px;
  margin: 0 0 0 5px;
}
.tab-menu .tab-content .tab-item {
  display: none;
  padding: 10px;
}
.tab-menu .tab-content .tab-item.active {
  display: block;
}
.list-container {
  border: 1px solid #ccc;
  border-radius: 3px;
}
.list-container.active {
  border-color: rgba(52, 152, 219, 0.75);
}
.list-container .list-input input {
  border: 0;
  border-bottom: 1px solid #ccc;
  border-radius: 0;
}
.list-container .list-items {
  padding: 5px;
  border-top: none;
  height: 150px;
  overflow-y: auto;
}
.list-container .list-items .no-items {
  color: #999;
  font-style: italic;
}
.list-container .list-items .list-item {
  padding: 3px 5px;
}
.list-container .list-items .list-item:hover {
  background: rgba(0, 0, 0, 0.1);
  cursor: pointer;
}
.list-container .list-items .list-item:hover .item-remove {
  display: block;
}
.list-container .list-items .list-item .item-index {
  color: #999;
  display: block;
  margin: 0  5px 0 0;
}
.list-container .list-items .list-item .item-value {
  display: block;
}
.list-container .list-items .list-item .item-remove {
  display: none;
  color: #999;
}
.list-container .list-items .list-item .item-remove:hover {
  color: #000;
}
* {
  font-family: 'Open Sans', sans-serif;
  box-sizing: border-box;
  font-size: 14px;
}
html,
body {
  margin: 0;
  padding: 0;
  background: #eee;
}
#app {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  width: 100%;
  height: 100%;
  overflow-y: auto;
}
nav {
  width: 150px;
  border-right: 1px solid #ccc;
  background: #fff;
}
nav ul {
  list-style: none;
  margin: 1rem 0;
  padding: 0;
}
nav ul li {
  font-size: 14px;
}
nav ul li.active {
  background: rgba(0, 0, 0, 0.05);
}
nav ul li a {
  color: #333;
  text-decoration: none;
  display: block;
  padding: 0.25rem 0.5rem;
  outline: 0 none;
}
nav ul li a span.badge {
  background: rgba(0, 0, 0, 0.1);
  padding: 0 7px;
  border-radius: 50px;
}
main {
  padding: 10px;
}
